import Vue from 'vue'
import Router from 'vue-router'
import Contacts from '@/components/Contacts'
import About from '@/components/About'


Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      component: About
    },
    {
      path: '/about',
      component: About
    },
    {
      path: '/contacts',
      component: Contacts
    }
  ]
})



