<template>
  <nav>
    <ul>
      <li v-for="link in menu" :key="link.menuItem" :class="link.menuItem">
        <router-link :to="createLink(link.menuItem)" >{{link.title}}</router-link>
      </li>
    </ul>
  </nav>
</template>

<script>

  export default {
    data() {
      return {
        active: 'about',
        menu: [
          {
            title: 'about',
            menuItem: 'about',
            showModal: false
          },
          {
            title: 'contacts',
            menuItem: 'contacts',
            showModal: false
          }]
      }
    },
    mounted() {

    },
    methods: {
      createLink(menuItem) {
        return "/" + menuItem;
      },
      makeActive(item) {
        this.active = item;
      }
    }

  }
</script>

<style scoped>
  * {
    margin: 0;
    padding: 0;
  }

  nav {
    margin: 50px;
    display: inline-block
  }

  ul {
    overflow: auto;
    list-style-type: none;
  }

  li {
    height: 25px;
    float: left;
    margin-right: 0px;
    border-right: 1px solid #aaa;
    padding: 0 20px;
  }

  li:last-child {
    border-right: none;
  }

  li a {
    text-decoration: none;
    color: #ccc;
    font: 25px/1 Helvetica, Verdana, sans-serif;
    text-transform: uppercase;
    -webkit-transition: all 0.5s ease;
  }

  li a:hover {
    text-decoration: none;
    color: #BF1E2E;
  }
</style>



